<template lang="html">
  <a class="twitter-timeline" :href="url">
    Tweets by {{ text }}
  </a>
</template>

<script>
export default {
  props: ['url', 'src', 'text'],
  created: function () {
    let recaptchaScript = document.createElement('script')
    recaptchaScript.setAttribute('src', this.src)
    document.head.appendChild(recaptchaScript)
  }
}
</script>

