<template lang="pug">
  div(
      :style="style"
    )
    slot
    //- p {{ style }}
</template>

<script>
export default {
  data: function () {
    return {
      style: ""
    }
  },
  props: ['width', 'height', 'shift', 'shadowHeight', 'blur', 'color'],
  created () {
    let { width, height, shift, shadowHeight, blur, color } = this
    blur = blur || '0px'
    color = color || 'dimgray'
    width = width || 'auto'
    height = height || 'auto'
    shadowHeight = shadowHeight || '0px'
    shift = shift || '0px'
    this.style = {
      width,
      height,
      boxShadow: `${ shift } ${ shift } ${ blur } ${ shadowHeight } ${ color }`,
    }
  }
}
</script>

<style lang="sass" scoped>
  img
    width: 100%
    display: block
</style>
