<template lang="pug">
  a(:href="url" class="twitter-follow-button" data-show-count="false")
    |Follow {{ text }}
    //- なくてもいい ↑
</template>

<script>
export default {
  props: ['url', 'src', 'text'],
  created: function () {
    let recaptchaScript = document.createElement('script')
    recaptchaScript.setAttribute('src', this.src)
    document.head.appendChild(recaptchaScript)
  }
}
</script>

